.chart-bar {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 15px;
  }
  
  .chart-bar__inner {
    height: 100%;
    width: 100%;
    border: 1px solid black;
    border-radius: 16px;
    background-color: white;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }
  
  .chart-bar__fill {
    background-color: #f5bd45;
    width: 100%;
    transition: all 0.3s ease-out;
  }
  
  .chart-bar__label {
    color: black;
    font-weight: bold;
    font-size: 1rem;
    text-align: center;

    width: 75px;
    height: 35px;
    margin: 5px 10px;
    border: none;
  }

  .chart-bar__label:hover{
    cursor: pointer;
    background-color: #f5bd45;
    border: 1px black solid;
    border-radius: 20px;
  }

.selected{
    background-color: #f5bd45;
    border: 1px black solid;
    border-radius: 20px;
  }

  .selected:hover{
    background-color: white;
  }