@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  color: #95c4c3;
  margin: 0;
  background-color: #e1e3e1;
}


.header {
   display: inline-table;
  background-color: #95c4c3;
  color: black;
  min-width: 100%;
  height: 100px;
  top: 0;
  left: 0;
  margin-bottom: 20px;
  padding: 25px; 

  /* border-bottom: 5px solid #376160; */
  /* border-bottom: 5px solid #f5bd45; */
  /* border-bottom: 5px solid white; */
}

.left{
  float: left;
  text-align: center;
 
  display: flex;
  flex-direction: right;
  justify-content: space-evenly;
  margin: 10px auto;
  padding: 0 25px;
}

.right{
    text-align: right;
    justify-content: right;
    justify-self: right;
    margin: 0px;
    /* background-color: white; debugging */
    float: right;
    min-width: 25%;
    /* display: inline-flex; */
}

.input{
  border-radius: 20px;
  padding: 5px;
  margin: 25px 10px;
  text-align: center;
}

.right button {
float: right;
  font: inherit;
  cursor: pointer;
  padding: 1rem 2rem;
  border: 2px solid #376160;
  background-color: white;
  color: #376160;
  border-radius: 12px;
  margin-right: 1rem;
}

.right button:hover {
  border: 2px solid white;
  color: white;
  background-color: #376160;
  border-color: #376160;
}

.expense-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.5rem;
    margin: 0;
    background-color: #FFFF;
  }
  
  .expense-item__description {
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
    align-items: flex-end;
    flex-flow: column-reverse;
    justify-content: flex-start;
    flex: 1 1;
  }

  .expense-item__description__edit{
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
    align-items: flex-end;
    flex-flow: column-reverse;
    justify-content: flex-start;
    flex: 1 1;

    text-align: center;
    font-size: xx-large;
    border: 1px black solid;
    margin: 0px 25px;
  }
  
  .expense-item h2 {
    color: #3a3a3a;
    font-size: 1rem;
    flex: 1 1;
    margin: 0 1rem;
    color: #3a3a3a;
  }
  
  .expense-item__price {
    font-size: 1rem;
    font-weight: bold;
    color: white;
    background-color: #15942c;
    border: 1px solid white;
    padding: 0.5rem;
    border-radius: 12px;
    
  }

  .expense-item__price input{
    background-color: #15942c;
    text-align: center;
    color: white;
    font-size: larger;
    font-weight: bold;

    width:100px;
    border: none;
  }

  .removeBtn{
    padding: 10px;
    margin: 10px;
    width: 35px;
    height: 35px;
    background-color: white;
    color: black;

    border-radius: 10px;
    border: 1px solid black;
    -webkit-text-emphasis: bolder;
            text-emphasis: bolder;
  }

  .removeBtn:hover{
    background-color: red;
    color: white;
    border-color: white;
    cursor: pointer;
  }

  .editBtn{
    padding: 10px;
    margin: 10px;
    background-color: white;
    color: black;
    text-align: center;

    border-radius: 10px;
    border: 1px solid black;
    -webkit-text-emphasis: bolder;
            text-emphasis: bolder;
  }

  .editBtn:hover{
    background-color: #f5bd45;
    color: white;
    border-color: white;
    cursor: pointer;
  }
  
  @media (min-width: 580px) {
    .expense-item__description {
      flex-direction: row;
      align-items: center;
      justify-content: flex-start;
      flex: 1 1;
    }
  
    .expense-item__description h2 {
      font-size: 1.25rem;
    }
  
    .expense-item__price {
      font-size: 1.25rem;
      padding: 0.5rem 1.5rem;
    }
  }
.card{
    /* border-radius: 12px; */
    /* border: 0.5px solid black; */
    border-top: 1px solid rgba(0, 0, 0, .25);
    margin: 5px auto;
    /* box-shadow: 0 1px 8px rgba(0, 0, 0, .25); */
}
.expense-date {
    display: flex;
    flex-direction: column;
    width: 5.5rem;
    height: 5.5rem;
    border: 1px solid #ececec;
    background-color: #95c4c3;
    color: white;
    border-radius: 12px;
    align-items: center;
    justify-content: center;
  }
  
  .expense-date__month {
    font-size: 0.75rem;
    font-weight: bold;
  }
  
  .expense-date__year {
    font-size: 0.75rem;
  }
  
  .expense-date__day {
    font-size: 1.5rem;
    font-weight: bold;
  }
.new-expense__controls {
    color: black;
    display: flex;
    flex-wrap: wrap;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 1rem;
    text-align: left;
  }
  
  .new-expense__control label {
    font-weight: bold;
    margin-bottom: 0.5rem;
    display: block;
  }
  
  .new-expense__control input {
    font: inherit;
    padding: 0.5rem;
    border-radius: 6px;
    border: 1px solid #ccc;
    width: 20rem;
    max-width: 100%;
  }
  
  .new-expense__actions {
    text-align: center;
  }

  .form-title{
      text-align: left;
      color: black;
      border-bottom: 2px solid #e1e3e1;
      border-width: 100%;
      margin-bottom: 10px;
  }

  .new-expense__actions button {
    font: inherit;
    cursor: pointer;
    padding: 1rem 2rem;
    border: 2px solid #376160;
    background-color: white;
    color: #376160;
    border-radius: 12px;
    margin-right: 1rem;
  }
  
  .new-expense__actions button:hover,
  .new-expense__actions button:active {
    border: 2px solid white;
    color: white;
    background-color: #f5bd45;
    border-color: #f5bd45;
    cursor: pointer;
  }

  #cancelBtn:hover{
    border: 2px solid white;
    color: white;
    background-color: red;
    border-color: red;
    cursor: pointer;
  }
.expenses-list {
    list-style: none;
    padding: 0;
  }
  
  .expenses-list__fallback {
    color: black;
    text-align: center;
  }
.expenses-filter {
    color: black;
    padding: 0 1rem;
  }
  
  .expenses-filter__control {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    margin: 1rem 0;
  }
  
  .expenses-filter label {
    font-weight: bold;
    margin-bottom: 0.5rem;
  }
  
  .expenses-filter select {
    font: inherit;
    padding: 0.5rem 3rem;
    font-weight: bold;
    border-radius: 6px;
  }
.chart {
    padding: 1rem;
    border-radius: 12px;
    background-color: white;
    text-align: center;
    display: flex;
    justify-content: space-around;
    height: 10rem;
  }
.chart-bar {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 15px;
  }
  
  .chart-bar__inner {
    height: 100%;
    width: 100%;
    border: 1px solid black;
    border-radius: 16px;
    background-color: white;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }
  
  .chart-bar__fill {
    background-color: #f5bd45;
    width: 100%;
    transition: all 0.3s ease-out;
  }
  
  .chart-bar__label {
    color: black;
    font-weight: bold;
    font-size: 1rem;
    text-align: center;

    width: 75px;
    height: 35px;
    margin: 5px 10px;
    border: none;
  }

  .chart-bar__label:hover{
    cursor: pointer;
    background-color: #f5bd45;
    border: 1px black solid;
    border-radius: 20px;
  }

.selected{
    background-color: #f5bd45;
    border: 1px black solid;
    border-radius: 20px;
  }

  .selected:hover{
    background-color: white;
  }
.new-expense {
    /* background-color: #95c4c3; */
    padding: 1rem;
    margin: 2rem auto 0 auto;
    width: 50rem;
    max-width: 95%;
    border-radius: 12px;
    text-align: center;
    /* box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25); */
  }
  
  .new-expense button {
    font: inherit;
    cursor: pointer;
    padding: 1rem 2rem;
    border: 2px solid #376160;
    background-color: white;
    color: #376160;
    border-radius: 12px;
    margin-right: 1rem;
  }
  
  .new-expense button:hover,
  .new-expense button:active {
    border: 2px solid white;
    color: white;
    background-color: #376160;
    border-color: #376160;
  }

